<template>
	<div>
		<!--main-->
		<div class="container center banner">
			<img src="../../static/img/banner1.webp" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner1.webp" class="img-responsive m-bg" />
		</div>
		<div class="container center bread-nav">
			<span>当前位置:</span><span><router-link to="/sc_info">学院概况</router-link></span><span>></span><span><router-link to ="/campus_style">校园文体</router-link></span><span>></span><span><a href="#">社团风采</a></span>
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<img src="../../static/img/item2.png" class="img-responsive" />
					<ul class="list-unstyled">
						<li>
							<router-link to ="/campus_style">
								<i class="iconfont icon-sjx-r"></i><span>主题教育</span>
							</router-link>
						</li>
						<li>
							<router-link to ="/co_construction">
								<i class="iconfont icon-sjx-r"></i><span>集体建设</span>
							</router-link>
						</li>
						<li class="active">
							<router-link to ="/community_mien">
								<i class="iconfont icon-sjx-r"></i><span>社团风采</span>
							</router-link>
						</li>
						<li>
							<router-link to="/campus_tour">
								<i class="iconfont icon-sjx-r"></i><span>校园风貌</span>
							</router-link>
						</li>
					</ul>
					<div class="zhaosheng">
						<a href="#">
							<img src="../../static/img/zhaosheng1.png" class="img-responsive" />
						</a>
						<a href="#">
							<img src="../../static/img/zhaosheng2.png" class="img-responsive" />
						</a>
					</div>
				</div>
				<!--mobail-->
				<div class="m-item-nav">
					<ul>
						<li>
							<router-link to ="/campus_style">
								<span>主题教育</span>
							</router-link>
						</li>
						<li>
							<router-link to ="/co_construction">
								<span>集体建设</span>
							</router-link>
						</li>
						<li class="active">
							<router-link to ="/community_mien">
								<span>社团风采</span>
							</router-link>
						</li>
						<li>
							<router-link to="/campus_tour">
								<span>校园风貌</span>
							</router-link>
						</li>
					</ul>
				</div>	
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h4>社团风采</h4>
				</div>
				<div class="theme-list">
					<span id="none">资料正在整理中..</span>
					<div v-for="item in nowList">
						<div class="col-lg-2 col-md-3 col-sm-3 col-xs-4">
							<img :src="item.pic_thumb" class="img-responsive" />
						</div>
						<div class="col-lg-10 col-md-9 col-sm-9 col-xs-8 news-dialog">
							<router-link :to="{name: 'news4', params: { id: item.id }}">
								<div class="tl-title">
									<span class="h4">{{item.title}}</span>
									<span class="text-right">{{item.add_time}}</span>
								</div>
								<p>{{item.synopsis}}</p>
							</router-link>
						</div>
					</div>
				</div>
				
				<!--fenye-->
				<nav class="p-page" aria-label="Page navigation" id="page">
				  <ul class="pagination">
				    <li @click="jump(curPage==1?1:curPage-1)">
				      <a href="#" aria-label="Previous">
				        <span aria-hidden="true">&laquo;</span>
				      </a>
				    </li>
				    <li v-for="item in totalPageList" @click="jump(item)" :class="{active:curPage==item}"><a href="#">{{item}}</a></li>
				    <li @click="jump(curPage==totalPage?totalPage:curPage+1)">
				      <a href="#" aria-label="Next">
				        <span aria-hidden="true">&raquo;</span>
				      </a>
				    </li>
				  </ul>
				</nav>
				<nav class="m-page" aria-label="..." id="page1">
				  <ul class="pager">
				    <li @click="jump(curPage==1?1:curPage-1)"><a href="#">&laquo;上一页</a></li>
				    <li @click="jump(curPage==totalPage?totalPage:curPage+1)"><a href="#">下一页&raquo;</a></li>
				  </ul>
				</nav>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
        return{
        	newsList: [],
        	curPage:1,
			total:'',
			show:6,
			nowList:[],
			totalPage:'',
			totalPageList:[],
        }
    },
	created:function(){
		document.title="社团风采_扬州中瑞酒店职业学院互联网学院";
    	var _self = this;
　　　	var url="http://hh.c1903.com/index/api/article";            
		  this.$http.post(url,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2',type:'9'}
			).then(function (data) {
        	var json = data.body;             
		  	_self.newsList = json;
		  	if(_self.newsList.length == 0){
				var none = document.getElementById('none');
				none.style.display = 'block';
				document.getElementById('page').style.display = "none";
				document.getElementById('page1').style.display = "none";
				return false;
				}
			  	//if(_self.newsList.length == 0)
				this.total = _self.newsList.length;
				//生成多少页
				this.totalPage = this.total/this.show;
				if(this.totalPage <= 1){
					document.getElementById('page').style.display = "none";
					document.getElementById('page1').style.display = "none";
				}
				for(var i = 0;i<this.totalPage;i++){
					this.totalPageList.push(i+1);
				}
				
				//每一页装多少条
				if(this.total < this.show)
					this.show = this.total
				for(var i =0;i<this.show;i++){
					this.nowList.push(_self.newsList[i]);
				}
    	});          
   
   
    },
   	methods:{
   		jump(page){
			this.curPage = page;
			this.nowList = [];
			var star = (page-1)*this.show;
			var end = (page-1)*this.show+this.show;
			if(end > this.total)
				end = this.total;
			for (var i = star;i<end;i++) {
				this.nowList.push(this.newsList[i]);
			}
		}
   	},
	activated(){
		 document.documentElement.scrollTop = 0
	}
}
</script>

<style>
</style>


